(function(){
  'use strict';

  angular
  .module('hiAngular')
  .factory('spotifyAPI', spotifyAPI);

  function spotifyAPI($http){
    var spotifyAPI = {};

    spotifyAPI.getArtists = function(query){
      return $http({
        url:'https://api.spotify.com/v1/search?q='+query+'&type=artist'
      });
    }

    spotifyAPI.getResults = function(query, type, callback){
      var results = [];
      $http({
        url:'https://api.spotify.com/v1/search?q='+query+'&type='+type
      }).success(function(response){
        if (type == 'Artist'){
          if(response.artists.items){
            var artists = response.artists.items;
            for (var i = 0; i < artists.length; i++){
              var artist = {
                name: artists[i].name,
                url: artists[i].external_urls.spotify,
                detail: 'Popularity: ' + artists[i].popularity,
              };
              artist.image = (artists[i].images.length > 0) ? artists[i].images[0].url : null;
              results.push(artist);
            }
            callback(results);
          }
        }
        else if (type ==  'Track'){
          if(response.tracks.items){
            var tracks = response.tracks.items;
            for (var i = 0; i < tracks.length; i++){
              var track = {
                name: tracks[i].name,
                url: tracks[i].external_urls.spotify,
                detail: 'Artist: ' + tracks[i].artists[0].name + ' | Album: ' + tracks[i].album.name,
              };
              track.image = (tracks[i].album.images.length > 0) ? tracks[i].album.images[0].url : null
              results.push(track);
            }
            callback(results);
          }
        }
        else if (type ==  'Album'){
          if(response.albums.items){
            var albums = response.albums.items;
            for (var i = 0; i < albums.length; i++){
              var album = {
                name: albums[i].name,
                url: albums[i].external_urls.spotify,
                detail: 'Album type: ' + albums[i].album_type,
              };
              album.image = (albums[i].images.length > 0) ? albums[i].images[0].url : null
              results.push(album);
            }
            callback(results);
          }
        }
      })}
      return spotifyAPI;
    };
  }
)();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function(){
  'use strict';

  angular
    .module('hiAngular')
    .factory('ergastAPI', ergastAPI);

  function ergastAPI($http){

    var ergastAPI = {};

    ergastAPI.getDrivers = function() {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2013/driverStandings.json?callback=JSON_CALLBACK'
      });
    }

    ergastAPI.getDriverDetails = function(id) {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2013/drivers/'+ id +'/driverStandings.json?callback=JSON_CALLBACK'
      });
    }

    ergastAPI.getDriverRaces = function(id) {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2013/drivers/'+ id +'/results.json?callback=JSON_CALLBACK'
      });
    }

    return ergastAPI;
  }
})();

(function(){
  'use strict';

  angular
  .module('hiAngular')
  .controller('SpotifyController', SpotifyController);

  function SpotifyController(spotifyAPI) {
    var vm = this;

    vm.searchType = 'Artist';
    vm.searchTypes = ['Artist', 'Track', 'Album'];
    vm.results = [];
    vm.loaded = true;
    vm.resultsEmpty = false;

    vm.searchChange = function(){
      if(vm.search && vm.searchType){
        vm.loaded = vm.resultsEmpty = false;
        spotifyAPI.getResults(vm.search,vm.searchType,function (results){
          vm.resultsEmpty = ( results.length > 0  ? false : true);
          vm.results = results;
          vm.loaded = true;
        });
      }
    };
  };
})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1462589647277;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .controller('DriversController', DriversController);

  /** @ngInject */
  function DriversController(ergastAPI) {
    var vm = this;

    vm.nameFilter = null;
    vm.driversList = [];

    ergastAPI.getDrivers().success(function(response){
        vm.driversList = response.MRData.StandingsTable.StandingsLists[0].DriverStandings;
    });

    vm.searchFilter = function (driver) {
        var keyword = new RegExp(vm.nameFilter, 'i');
        return !vm.nameFilter || keyword.test(driver.Driver.givenName) || keyword.test(driver.Driver.familyName);
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .controller('DriverController', DriverController);

  /** @ngInject */
  function DriverController(ergastAPI, $routeParams) {
    var vm = this;

    vm.id = $routeParams.id;
    vm.races = [];
    vm.driver = null;
    vm.loaded = false;

    ergastAPI.getDriverDetails(vm.id).success(function(response){
        vm.driver = response.MRData.StandingsTable.StandingsLists[0].DriverStandings[0];
    });

    ergastAPI.getDriverRaces(vm.id).success(function(response){
        vm.races = response.MRData.RaceTable.Races;
        vm.loaded = true;
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('hiAngular')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', { templateUrl: 'app/main/main.html', controller: 'MainController', controllerAs: 'main' })
      .when('/drivers', { templateUrl: 'app/drivers/drivers.html', controller: 'DriversController', controllerAs: 'drivers' })
      .when('/drivers/:id', { templateUrl: 'app/driver/driver.html', controller: 'DriverController', controllerAs: 'driver' })
      .when('/spotify', { templateUrl: 'app/spotify/spotify.html', controller: 'SpotifyController', controllerAs: 'spotify' })
      .otherwise({ redirectTo: '/' });
  }

})();

